/*global chrome*/
import React, { Component } from 'react';
import './ChatboxIframe.css'
import storage from './storage.js'
const CHATBOX_FRAME_SRC = 'chatbox-only/chatbox-frame.html'
const FOR_SAME_PAGE_EXTENSION = true
const CREATE_IFRAME_BY_DEFAULT = FOR_SAME_PAGE_EXTENSION
class ChatboxIframe extends Component {
  state = {
    // default state must be false, because we don't 
    // want the render function to create iframe before reading
    // the config from local storage
    createChatboxIframe: false
  };
  locationHref = window.location.href;
  constructor(props) {
    super(props);
    this.iframeRef = React.createRef();
    window.openChatbox = this.openChatbox;
  }
  openChatbox = () => {
    // if iframe not created, create iframe and tell iframe to big full size
    // if iframe already created, tell iframe to become full size
    if (!chrome.extension) return;
    if (this.state.createChatboxIframe) {
      this.iframeRef.current.contentWindow.postMessage({chatboxMsg: 'open_chatbox'}, "*");
    } else {
      this.setState({createChatboxIframe: true});
      chrome.storage.local.get('chatbox_config', (item) => {
          var configData = item['chatbox_config'] || {};
          var redirectDict = {};
          redirectDict[window.location.href] = true;
          configData['redirect'] = redirectDict;
          chrome.storage.local.set({'chatbox_config': configData});
      }); 
    } 
  }
  keepCheckingLocation = () => {
    // always post title and url to iframe because
    // the iframe may not listen and get the first event..
    // todo: improve this
    this.locationHref = window.location.href;
    var msg = {
        'locationUpdate': true,
        'title': document.title,
        'url': window.location.href
    }
    if (this.iframeRef.current) {
      this.iframeRef.current.contentWindow.postMessage(msg, "*");
    }
    setTimeout(()=>{
      this.keepCheckingLocation();
    }, 3000);
  }
  resizeIFrameToFitContent = (e) => {
    if (!this.iframeRef.current) return;
    if (!e || !e.data )
        return;
    var msg = e.data;
    if (!msg.state)
        return;
        
    if (msg.state ==='show') {
        this.iframeRef.current.style.display  = "block";
        console.log('show iframe');
    }
    else if (msg.state ==='full size') {
        this.iframeRef.current.style.display  = "block";
        this.iframeRef.current.style.width  = "100%"; //dragging may show shadow if drag too left
        this.iframeRef.current.style.height = "100%";
        console.log('resize iframe to 100%');
    }
    else if (msg.state ==='show modal') {
        this.iframeRef.current.style.display  = "block";
        this.iframeRef.current.style.width  = "100%"; //dragging may show shadow if drag too left
        this.iframeRef.current.style.height = "100%";
        this.iframeRef.current.style.left = "0px";
        console.log('resize iframe to 100% and center');
    }
    else if (msg.state === 'minimize') {
        this.fitChatboxIframe(msg);
    }
    else if (msg.state === 'close') {   //only hide but still running
        this.iframeRef.current.style.display  = "none";
        console.log('hide iframe');
    }
    else if (msg.state === 'moving') {
        this.moveFrame(msg.left);
    }
    else if (msg.state === 'fit') { // fit - make page same size as chatbox
        this.fitChatboxIframe(msg);
    }
  }
  moveFrame = (left) => {
    let chatboxLeft = left;
    if (chatboxLeft < 0) chatboxLeft = 0;
    let maxLeft = window.innerWidth - 200;
    if (chatboxLeft > maxLeft) chatboxLeft = maxLeft;
    this.iframeRef.current.style.left = chatboxLeft + 'px';
  }

  fitChatboxIframe = (msg) => {
    if (!this.iframeRef.current) return;
    this.iframeRef.current.style.display  = "block";
    this.iframeRef.current.style.width  = msg.width;
    this.iframeRef.current.style.height = msg.height;
    this.iframeRef.current.style.left = msg.left + 'px';
    this.moveFrame(msg.left);
  }
  handleTabVisibilityChange = () => {
    if (!document.hidden && !this.state.createChatboxIframe) {
      chrome.runtime.sendMessage({ "clearIcon" : true });
    }
  }
  componentDidMount() {
    this.keepCheckingLocation();
    window.addEventListener("message", this.resizeIFrameToFitContent, false);
    storage.getConfigFromStorage('autoJoinLiveChat', (val) => {
      if (val == null) {
        this.setState({createChatboxIframe: CREATE_IFRAME_BY_DEFAULT});
      } else {
        this.setState({createChatboxIframe: val});
      }
    })
    if (chrome.extension) {
      chrome.runtime.sendMessage({ "clearIcon" : true });
      document.addEventListener('visibilitychange', this.handleTabVisibilityChange, false);
      
      chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
          if (!request.chatboxMsg||this.state.createChatboxIframe) return;
          var msg = request.chatboxMsg;
          if (msg == "open_chatbox") {
            this.openChatbox();
            sendResponse({msg: "shown"});
          }
      });
    }
  }
  render() {
    let chatboxIframe = ''
    if (this.state.createChatboxIframe && chrome.extension) {
      console.log('render chatbox iframe!');
      chatboxIframe = <iframe ref={this.iframeRef} className='sp-chatbox-iframe' src={chrome.extension.getURL(CHATBOX_FRAME_SRC)+"?"+this.locationHref}/> 
    }
    return (
      <div>{chatboxIframe}</div>
    )
  }
}

export default ChatboxIframe;