/*global chrome*/
import React, { Component } from 'react';
// import {drawImage} from 'canvas-object-fit';

/**
 * Draws a rounded rectangle using the current state of the canvas.
 * If you omit the last three params, it will draw a rectangle
 * outline with a 5 pixel border radius
 * @param {CanvasRenderingContext2D} ctx
 * @param {Number} x The top left x coordinate
 * @param {Number} y The top left y coordinate
 * @param {Number} width The width of the rectangle
 * @param {Number} height The height of the rectangle
 * @param {Number} [radius = 5] The corner radius; It can also be an object 
 *                 to specify different radii for corners
 * @param {Number} [radius.tl = 0] Top left
 * @param {Number} [radius.tr = 0] Top right
 * @param {Number} [radius.br = 0] Bottom right
 * @param {Number} [radius.bl = 0] Bottom left
 * @param {Boolean} [fill = false] Whether to fill the rectangle.
 * @param {Boolean} [stroke = true] Whether to stroke the rectangle.
 */
function roundRect(ctx, x, y, width, height, radius, fill, stroke) {
  if (typeof stroke == 'undefined') {
    stroke = true;
  }
  if (typeof radius === 'undefined') {
    radius = 5;
  }
  if (typeof radius === 'number') {
    radius = {tl: radius, tr: radius, br: radius, bl: radius};
  } else {
    var defaultRadius = {tl: 0, tr: 0, br: 0, bl: 0};
    for (var side in defaultRadius) {
      radius[side] = radius[side] || defaultRadius[side];
    }
  }
  ctx.beginPath();
  ctx.moveTo(x + radius.tl, y);
  ctx.lineTo(x + width - radius.tr, y);
  ctx.quadraticCurveTo(x + width, y, x + width, y + radius.tr);
  ctx.lineTo(x + width, y + height - radius.br);
  ctx.quadraticCurveTo(x + width, y + height, x + width - radius.br, y + height);
  ctx.lineTo(x + radius.bl, y + height);
  ctx.quadraticCurveTo(x, y + height, x, y + height - radius.bl);
  ctx.lineTo(x, y + radius.tl);
  ctx.quadraticCurveTo(x, y, x + radius.tl, y);
  ctx.closePath();
  if (fill) {
    ctx.fill();
  }
  if (stroke) {
    ctx.stroke();
  }

}
function circleImg(ctx, img, x, y, r) {
  ctx.save();
  var d =2 * r;
  var cx = x + r;
  var cy = y + r;
  ctx.beginPath();
  ctx.arc(cx, cy, r, 0, 2 * Math.PI);
  ctx.closePath();
  ctx.clip();
  ctx.drawImage(img, x, y, d, d);
  ctx.restore();
}
function getWindowWidth () {
  return document.documentElement.scrollWidth;
  // return Math.max(window.innerWidth, document.body.clientWidth);
}
class Canvas extends Component {
  CANVAS_HEIGHT = 500;
  ROW_NUM = 8;
  danmuList = []; // danmu already on canvas
  // danmu waiting to be added to canvas, should add to
  // canvas as soon as there's any spot
  danmuWaitList = []; 
  updatingCanvas = false; // no need to update canvas if no danmu
  videoPaused = false;

  constructor(props) {
    super(props);
    this.canvasRef = React.createRef();
    this.canvasContext = null; //init in componentDidMount
    window.queueCanvasDanmu = this.queueDanmu;
    window.setVideoState = this.setVideoState;
    window.clearVideoDanmu = this.clearVideoDanmu;
  }
  startDrawing = () => {
      if (this.updatingCanvas) return;
      this.updatingCanvas = true;
      this.draw();
  }
  draw = () => {
    // Only startDrawing can call this function!!!
    // Or this function itself - window.requestAnimationFrame(this.draw)
    const ctx = this.canvasContext;
    this.canvasRef.current.style.width=getWindowWidth()+'px';
    ctx.canvas.width  = getWindowWidth()*2;
    ctx.canvas.height = this.CANVAS_HEIGHT*2;;
    ctx.scale(2,2);
    this.canvasContext.font = '500 18px sans-serif';
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    // console.log(this.danmuList.length);
    this.danmuList.forEach(danmu => {
      if (danmu.live || !this.videoPaused) {
        danmu.x-=danmu.speed;
      }
      try {
        const paddingX = 10;
        if (danmu.img) {
          ctx.drawImage(danmu.img, danmu.x, danmu.y-20, 50, 50);
        } else {
        // console.log(danmu.content);
        // draw background box  
          if (danmu.live) {
            ctx.fillStyle = "rgba(0, 137, 255, 1)";
            // ctx.fillStyle = "rgba(255, 255, 255, 1)";
            ctx.strokeStyle = "lightgray";
            if (danmu.self) {
              ctx.fillStyle = "rgba(190, 255, 0, 1)";
            }

          } else {
            ctx.fillStyle = "rgba(0, 0, 0, 0.5)";
            ctx.strokeStyle = "lightgray";
          }   
          ctx.lineWidth = 0.5;
          roundRect(ctx, danmu.x-paddingX, danmu.y-20, danmu.width+paddingX*2, 30, 5, true, true);
          // draw text
          if (danmu.live) {
            ctx.fillStyle = "white";
            if (danmu.self) {
              ctx.fillStyle = "black";
            }
          } else {
            ctx.fillStyle = 'white';
          }   
          ctx.fillText(danmu.content, danmu.x, danmu.y);
        }
        if (danmu.avatar) {
          const avatarRadius = 20;
          circleImg(ctx, danmu.avatar, danmu.x-paddingX-5-avatarRadius*2, danmu.y-5-avatarRadius, avatarRadius);
        }
      } catch (err) {
        console.error(err);
      }
    })
    this.danmuList = this.danmuList.filter(danmu => {
        return danmu.x > - danmu.width - 50;
    })
    if (this.danmuList.length) {
        window.requestAnimationFrame(this.draw);
    } else {
        this.updatingCanvas = false;
    }
  }
  createDanmuObj = (msg) => {
    let danmu = {
        content: msg.content,
        avatarSrc: msg.profileImgSrc,
        x: getWindowWidth()+50, // plus avatar width
        y: 100,
        row: 1,
        width: 100,
        height: 30,
        live: msg.type === 'live',
        self: msg.me,
        speed: 2-Math.random()
    }
    return danmu;
  }
  findSpot = () => {
    let occupiedRows = {};
    const spacing = 50+30; // and avatar space
    this.danmuList.forEach(danmu => {
        let x = danmu.x + danmu.width + spacing;
        if (x >= getWindowWidth()) {
            occupiedRows[danmu.row] = true;
        }
    })
    for (let i = 1; i <= this.ROW_NUM; i++) {
        if (!occupiedRows[i])
            return i;
    }
    return null;
  }
  checkDanmuWaitlist = () => {
    // console.log('check')
    // Find spot and put danmu on canvas
    // set row, y, width, adjust content length
    // prepare avatar image
    while(this.danmuWaitList.length) {
        let availableRow = this.findSpot();
        if (availableRow) {
            // console.log('found spot');
            let danmu = this.danmuWaitList.shift();
            danmu.row = availableRow;
            danmu.y = 50*availableRow;
            // TODO break into multiple lines
            danmu.width = this.canvasContext.measureText(danmu.content).width;
            let counter = 0;
            while (danmu.width > 700 && counter++ < 10) {
                let longStr = danmu.content;
                danmu.content = longStr.slice(0, Math.ceil(longStr.length/2))+'...';
                danmu.width = this.canvasContext.measureText(danmu.content).width;
            }
            // avatar
            if (danmu.avatarSrc) {
              // TODO: object-fit: cover
              if (danmu.avatarSrc === 'profile-empty.png') {
                // if (chrome.extension) {
                //   danmu.avatarSrc = chrome.extension.getURL('chatbox-only/profile-empty.png');
                //   danmu.avatar = new Image();
                //   danmu.avatar.src = danmu.avatarSrc;
                // }
              } else {
                danmu.avatar = new Image();
                danmu.avatar.src = danmu.avatarSrc;
              }
            }
            if (danmu.content.startsWith('stickers/') && chrome.extension) {
              danmu.img = new Image();
              danmu.img.src = chrome.extension.getURL('chatbox-only/'+danmu.content);
            }
            this.danmuList.push(danmu);
            // console.log('show ' + danmu.content);
            this.startDrawing();
        } else {
            // console.log('no spot');
            setTimeout(this.checkDanmuWaitlist, 3000);
            return;
        }
    }
  }
  queueDanmu = (msg) => {
    // this function is called by other class
    // bind this to current class
    if (!this.canvasRef.current) {
      console.error('No canvas to use!');
      return;
    }
    // console.log('queue ' + msg.content);
    this.danmuWaitList.push(this.createDanmuObj(msg));
    this.checkDanmuWaitlist();
  }
  clearVideoDanmu = () => {
    // remove all video danmu in canvas and in waitlist
    this.danmuList = this.danmuList.filter(danmu => {
      return danmu.live;
    });
    this.danmuWaitList = this.danmuWaitList.filter(danmu => {
      return danmu.live;
    });
  }
  setVideoState = (play) => {
    this.videoPaused = !play;
  }
  receiveMsgFromChatboxFrame = (e) => {
    if (!e || !e.data )
        return;
    // e.stopPropagation(); // multiple danmu.js maybe
    var danmuMsg = e.data;
    if (danmuMsg.msg) {
      // console.log(danmuMsg);
      danmuMsg.msg.content = danmuMsg.msg.message;
      this.queueDanmu(danmuMsg.msg);
    }
    // username: socket.username,
    // message: cleanedMsg,
    // sender: socket.userId,
    // url: socket.roomId, // why need this field?
    // hasAvatar: socket.hasAvatar
  }
  componentDidMount() {
    if (!this.canvasRef.current) return;
    const canvas = this.canvasRef.current;
    this.canvasContext = canvas.getContext('2d');
    this.canvasContext.canvas.width  = getWindowWidth()*2;
    this.canvasContext.canvas.height = this.CANVAS_HEIGHT*2;;
    this.canvasContext.scale(2,2);
    this.canvasContext.font = '500 18px sans-serif';

    // window.addEventListener("message", this.receiveMsgFromChatboxFrame, false);
  
    if (chrome.extension) {
      chrome.storage.onChanged.addListener((changes, area) => {
          if ('videoDanmuEnabled' in changes) {
            const newVal = changes['videoDanmuEnabled']['newValue'];
            if (newVal) {
              this.showCanvas();
            } else {
              this.hideCanvas();
            }
          }
      });
    }
  }

  hideCanvas = () => {
    this.canvasRef.current.style.display = 'none';
  }

  showCanvas = () => {
    this.canvasRef.current.style.display = 'block';
  }

  render() {
    console.log('create canvas');
    return <canvas style={{pointerEvents: 'none', zIndex:2000, position: 'absolute', top: 50, left: 0, width:getWindowWidth()+'px', height: this.CANVAS_HEIGHT}} ref={this.canvasRef} />
  }
}

export default Canvas;