/*global chrome*/
import React, { Component } from 'react';

class Danmu extends Component {
  state = {
  }; 
  constructor(props) {
    super(props);
    this.danmuRef = React.createRef();
  }

  componentDidMount() {
    let startX = window.innerWidth;
    let duration = (startX+1000)/100;

    let danmuAnimation = this.danmuRef.current.animate([
            // keyframes, at least two
            { transform: 'translateX('+startX+'px)' }, 
            { transform: 'translateX(-1000px)' }
        ],
        {
            // timing options
            duration: duration*1000,
            // easing: 'ease-in-out'
        }
    );
    danmuAnimation.onfinish = () => {
        this.props.deleteSelf(this.props.danmu.id);
    }
    this.danmuRef.current.onmouseover = () => {
      danmuAnimation.pause();
    }
    this.danmuRef.current.onmouseout = () => {
      danmuAnimation.play();
    }
    this.danmuRef.current.onmousedown = () => {
      const onclickMsg = {
        'openChatbox': true,
        'type': this.props.danmu.type
      }
      if (chrome.extension) {
        document.getElementsByClassName('sp-chatbox-iframe')[0].contentWindow.postMessage(onclickMsg, "*");
      }
    }
  }
  render() {

    let avatar = '';
    if (this.props.danmu.avatarSrc) {
      avatar = <img className="sp-danmu-avatar" src={this.props.danmu.avatarSrc} />
    }
    let textContentClass = "sp-danmu-content-text"
    if (this.props.danmu.self) {
      textContentClass += ' self'
    }
    let content = <span className={textContentClass} dangerouslySetInnerHTML={{__html:this.props.danmu.content}}></span>
    if (this.props.danmu.img) {
      content = <img className="sp-danmu-content-img" src={this.props.danmu.imgSrc} />
    }
    return (
      <div className="sp-danmu-wrapper" style={{top: this.props.danmu.top}} ref={this.danmuRef}>
        {avatar}
        {content}
      </div>
    );
  }
}

export default Danmu;
