/*global chrome*/
import React, { Component } from 'react';
import axios from 'axios';

import storage from './storage.js';
import Drawer from '@material-ui/core/Drawer';
import CommentList from './CommentList';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';


import './CommentDrawer.css';

function getVideoId () {
    // Just use entire url at the moment
  const url = new URL(window.location.href);
  return url.searchParams.get("id")
  // return window.location.href;
}
function removeStaticAds () {
	//only for www.dnvod.tv ?
	console.log('removeStaticAds')
	var videoContainerQuery = document.getElementsByClassName('video-player');
	if (videoContainerQuery.length) {
		var videoContainer = videoContainerQuery[0];
		videoContainer.style.margin='0px';
		var videoSideAds = document.getElementsByClassName('player-side');
		while(videoSideAds.length) {
			videoSideAds[0].remove();
		}
		var videoPauseAds = document.getElementsByTagName('vg-pause-ads');
		while(videoPauseAds.length) {
			videoPauseAds[0].remove();
		}
	}
}
function secToTimestamp (totalSeconds) {
  let hours = Math.floor(totalSeconds / 3600);
  totalSeconds %= 3600;
  let minutes = Math.floor(totalSeconds / 60);
  let seconds = totalSeconds % 60;

  // If you want strings with leading zeroes:
  minutes = String(minutes).padStart(2, "0");
  hours = String(hours).padStart(2, "0");
  seconds = String(seconds).padStart(2, "0");
  return hours + ":" + minutes + ":" + seconds;
}
const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#2196F3'
    },
    secondary: {
      main: '#b5ff01'
    }
  }
});
class DuonaoCommentDrawer extends Component {
  
  state = { 
    showDrawer: false,
    commentList: [],
    danmuInput: '',
    currentTime: 0,
    submitting: false,
    submitted: null,
    login: false
  };
  commentDict = {};
  lastCheckedSec = -1;
  videoId = null;
  videoPlayer = null;
  showDanmu = true; // if false then won't get new comments
  tabVisible = true;
  danmuBar = null;

  constructor(props) {
    super(props);
    console.log('load Duonao comment drawer + button');
    if (chrome.extension) {
      this.registerChromeStorageChangeListener();
    }
  }
  registerChromeStorageChangeListener = () => {
    chrome.storage.onChanged.addListener((changes, area)=>{
      if ('chatbox_config' in changes) {
        const chatboxConfig = changes['chatbox_config']['newValue'];
        if (chatboxConfig['token']) {
          this.setState({login: true});
        } else {
          this.setState({login: false});
        }
      }
    })
  }
  showDrawer = () => {
    this.setState({
      showDrawer: true,
    });
  }
  onClose = () => {
    this.setState({
      showDrawer: false,
    });
  }
  postComment = () => {
    this.setState({submitting: true})
    const sec = this.state.currentTime;
    const content = this.state.danmuInput;
    let payload = {
      videoId: this.videoId,
      type: 'Duonao',
      content: content,
      uuid: 123,
      sec: sec
    }
    axios.post(this.props.danmuUrl, payload).then(res => {
      this.setState({
        danmuInput: '',
        submitted: true
      });
      const danmu = {
        sec: sec,
        name: 'You',
        timestamp: secToTimestamp(sec),
        content: content,
        key: sec+content,
        profileImgSrc: null        
      }
      if (sec in this.commentDict) {
        this.commentDict[sec].push(danmu);
      } else {
        this.commentDict[sec] = [danmu];
      }
      this.setCommentListFromDict();
      window.queueCanvasDanmu(danmu);
    }).catch(error => {
      console.error(error);
      this.setState({
        submitted: false
      });
    }).then(() => {
      this.setState({submitting: false});
      setTimeout(()=>{
        this.setState({submitted:null});
      }, 3500);
    });
  }
  getComments = () => {
    if (!(this.showDanmu && this.tabVisible)) return;
    const url = this.props.danmuUrl + '?videoId='+this.videoId;
    console.log('fetch Duonao comments');
    axios.get(url)
      .then(res => {
        const commentDict = res.data.comments;
        Object.keys(commentDict).forEach(sec => {
          commentDict[sec].forEach( c => {
            c.sec = sec;
            c.timestamp = secToTimestamp(sec);
            c.key = c.timestamp+'-'+c.name+'-'+c.content;
          });
        });
        this.setState({login: res.data.login});
        this.commentDict = commentDict;
        this.setCommentListFromDict();
        if (window.location.href.indexOf('localhost:3210')>=0) {
            this.state.commentList.forEach(c => {
                // window.queueCanvasDanmu(c);
            });
        }

      })
  }
  videoSeekTo = (sec) => {
    if (!this.videoPlayer) {
      console.error('videoPlayer is null');
      return;
    }
    window.clearVideoDanmu();
    this.videoPlayer.currentTime = sec;
  }
  registerVideoEvents = () => {
    if (!this.videoPlayer) {
        console.error('no video player');
        return;
    }
    this.videoPlayer.addEventListener('timeupdate', (e) => {
      const sec = Math.floor(e.target.currentTime);
      if (sec === this.lastCheckedSec)
        return; // avoid same sec trigger multiple times
      this.lastCheckedSec = sec;
      this.setState({currentTime: sec});
      if (this.tabVisible && sec in this.commentDict) {
        // console.log(sec);
        this.commentDict[sec].forEach(function(comment) {
          window.queueCanvasDanmu(comment);
        })
      }
    })
    this.videoPlayer.addEventListener('pause', (e) => {
      console.log('duonao paused');
      window.setVideoState(false);
    });
    this.videoPlayer.addEventListener('play', (e) => {
      window.setVideoState(true);
    });
  }
  setCommentListFromDict = () => {
    let commentList = [];
    Object.keys(this.commentDict).forEach(sec => {
      this.commentDict[sec].forEach( c => {
        commentList.push(c);
      });
    });
    this.setState({ commentList: commentList });
  }
  checkVideoChanged = () => {
    const newVideoId = getVideoId();
    if (newVideoId !== this.videoId) {
      console.log('video changed from '+this.videoId+' to '+newVideoId);
      this.videoId = newVideoId;
      window.clearVideoDanmu();
      this.commentDict = {};
      this.setState({ commentList: [] });
      if (this.videoId) {
        this.getComments();
      } 
    }

    let danmuBar = document.getElementById('sp-danmu-bar');
    if (danmuBar && !this.danmuBar) {
      this.danmuBar = danmuBar;
    }
    if (!danmuBar) {
      // danmu bar may be deleted by DOM when switching from
      // video page to front page
      danmuBar = this.danmuBar;
    }
    let currentVideoPlayer = document.getElementById('video_player');
    if (currentVideoPlayer) {
      danmuBar.style.display = 'block';

      // if didn't have video player, register events onto it
      if (!this.videoPlayer) {
        this.videoPlayer = currentVideoPlayer;
        this.registerVideoEvents(); 
        removeStaticAds();
      }
      // if on video page and haven't moved danmu bar, move it now
      let duonaoDataBarQuery = document.getElementsByTagName('app-video-user-data-bar');
      if (duonaoDataBarQuery.length) {
        let duonaoDataBarElm = duonaoDataBarQuery[0];
        if (danmuBar.parentElement.tagName.toLowerCase() !== 'app-video-user-data-bar' ||
            danmuBar.parentElement != duonaoDataBarElm) {
            console.log('Found Duonao data bar, move danmu bar');
            duonaoDataBarElm.insertBefore(danmuBar, duonaoDataBarElm.firstChild);
        }
      }
    } else {
      if (window.location.href.indexOf('localhost:3210')===-1) {
        danmuBar.style.display = 'none';
      }
    }
    this.videoPlayer = currentVideoPlayer;
    setTimeout(this.checkVideoChanged, 3*1000);
  }
  handleTabVisibilityChange = () => {
    this.tabVisible = !document['hidden'];
  }
  componentDidMount() {
    document.addEventListener('visibilitychange', this.handleTabVisibilityChange, false);
    storage.getConfigFromStorage('videoDanmuEnabled', (showDanmu)=> {
      if (showDanmu != null) {
        this.showDanmu = showDanmu;
      }
      this.checkVideoChanged();
    })
    if (chrome.extension) {
      chrome.storage.onChanged.addListener((changes, area) => {
          if ('videoDanmuEnabled' in changes) {
            const newVal = changes['videoDanmuEnabled']['newValue'];
            console.log('videoDanmuEnabled changed '+newVal);
            this.showDanmu = newVal;
          }
      });
    }
  }
  handleDanmuInputChange = (event) => {
    this.setState({danmuInput: event.target.value});
  }
  clearDanmuInput = () => {
    this.setState({danmuInput: ''});
  }
  render() {
    let danmuInputButtons = <span></span>
    if (this.state.danmuInput) {

      if (this.state.login) {
        let submitBtnText = '发射!';
        if (this.state.submitting) submitBtnText = '发射中...';
        danmuInputButtons = (
          <span className='sp-danmu-input-btn-group'>
            <Button variant="contained" onClick={this.clearDanmuInput} className="sp-button" >
              取消
            </Button>
            <Button disabled={this.state.submitting} variant="contained" color="primary" onClick={this.postComment} className="sp-button" >
              {submitBtnText}
            </Button>
          </span>
        )
      } else {
        danmuInputButtons = <span onClick={window.openChatbox} className='sp-danmu-input-not-login'>
                              请先登录
                            </span>
      }
    }
    if (this.state.submitted!=null) {
      let submitStatus = '发射成功!';
      let submitStatusClass = 'sp-danmu-input-sent';
      if (this.state.submitted) {
        submitStatusClass+=' success';
      } else {
        submitStatusClass+=' fail';
        submitStatus = '发射失败';
      }
      danmuInputButtons = <span className={submitStatusClass}>{submitStatus}</span>
    }
    return (
      <MuiThemeProvider theme={theme}>
        <div id="sp-danmu-bar" className="sp-duonao-bar">
          <Button style={{verticalAlign: 'top'}} variant="contained"  onClick={this.showDrawer} className="sp-button" >
            弹幕: <span className="sp-comment-count">{this.state.commentList.length}</span>
          </Button>
          <span id="sp-danmu-input">
            <span className="sp-danmu-input-timestamp">{secToTimestamp(this.state.currentTime)}</span>
            <TextField
              placeholder="输入弹幕"
              multiline
              style={{width: "300px", marginTop: "10px"}}
              value={this.state.danmuInput}
              onChange={this.handleDanmuInputChange}
            />
            {danmuInputButtons}
          </span>
          <Drawer ModalProps={{
            BackdropProps:{
              classes:{
                root:'sp-drawer-mask'
              }
            }
          }}
          style={{zIndex:2147483646}} anchor="right" open={this.state.showDrawer}
          onClose={this.onClose}>
            <div
              className='sp-no-outline'
              tabIndex={0}
              role="button"
              onKeyDown={this.onClose}
            >
              <CommentList comments={this.state.commentList} closeDrawer={this.onClose} commentClickHandler={this.videoSeekTo}/>
            </div>
          </Drawer>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default DuonaoCommentDrawer;
