/*global chrome*/

import React, { Component } from 'react';
import YoutubeCommentDrawer from './YoutubeCommentDrawer';
import DuonaoCommentDrawer from './DuonaoCommentDrawer';
import Canvas from './Canvas'
import AnimationDanmu from './AnimationDanmu';
import ChatboxIframe from './ChatboxIframe';

// const danmuUrl = 'http://localhost:9000/db/video_danmu';
const danmuUrl = 'https://api.yiyechat.com/db/video_danmu';
 
class App extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    let commentDrawer = <span></span>
    if (window.location.href.indexOf('youtube')>=0 ||
    window.location.href.indexOf('localhost:3210')>=0) 
    {
      commentDrawer = <YoutubeCommentDrawer danmuUrl={danmuUrl}/>
    } 
    if (window.location.href.indexOf('dnvod')>=0 )
    {
      commentDrawer = <DuonaoCommentDrawer danmuUrl={danmuUrl}/>
    }

    let canvas = <span></span>
    if (window.location.href.indexOf('youtube')>=0 ||
        window.location.href.indexOf('dnvod')>=0 ||
        window.location.href.indexOf('localhost:3210')>=0) 
    {
      canvas = <Canvas />
    }
    return (
      <span>
        {commentDrawer}
        {canvas}
        <ChatboxIframe />
        <AnimationDanmu />
      </span>
    );
  }
}

export default App;
