  /*global chrome*/
  const storage = {
    getConfigFromStorage: (key, callback) => {
        if (chrome.storage) {
            chrome.storage.local.get(key, (item) => {
                if (key in item) {
                callback(item[key]);
                } else {
                callback(null);
                }
            })
        } else {
        if (localStorage.hasOwnProperty(key)) {
            callback(JSON.parse(localStorage.getItem(key)));
        } else {
            callback(null);
        }
        }
    }
  }

  export default storage;
