import React, { Component } from 'react';

import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Link from '@material-ui/core/Link';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';

import CloseIcon from '@material-ui/icons/Close';
// import './CommmentList.css'

function CommentList(props) {
  const commentList = props.comments;
  const useStyles = makeStyles({
    list: {
      width: 300,
    },
    listHeader: {
      borderBottom: '1px solid lightgray',
      background: 'white',
      padding: 15,
      fontSize: '1.2rem'
    },
    closeDrawer: {
      position: 'absolute',
      top: 12,
      left: 10,
      cursor: 'pointer',
      '&:hover': {
        color: 'black'
      }
    },
    listItem: {
      borderBottom: '1px solid lightgray',
    },
    avatar: {
      // width: 30,
      // height: 30
    },
    commentName: {
      overflow: 'hidden',
      maxWidth: 150,
      whiteSpace: 'nowrap',
      fontSize: '1.2rem',
      textOverflow: 'ellipsis',
      marginBottom: 5,
      color:'#0a0a0a',
      fontWeight: 500
    },
    commentContent: {
      color:'#111111',
      fontSize: '1.2rem',
      wordBreak: 'break-word'
    },
    timestamp: {
      position: 'absolute',
      color: '#0273ff !important',
      right: 10,
      fontSize: '1rem',
      cursor: 'pointer',
      '&:hover': {
        textDecoration: 'underline !important'
      }
    }
  });
  const classes = useStyles();

  return (
    <div>
      <List dense={true} className={classes.list}>
        <ListSubheader className={classes.listHeader}>
          <CloseIcon onClick={props.closeDrawer} className={classes.closeDrawer}/>
          <center><Typography style={{fontSize:"1.4rem"}}>Comments with timestamp ({commentList.length})</Typography></center>
        </ListSubheader>
        {commentList.map((comment, index) => (
          <ListItem className={classes.listItem}  key={comment.key} alignItems="flex-start">
            <ListItemAvatar>
              <Avatar className={classes.avatar} src={comment.profileImgSrc} />
            </ListItemAvatar>
            <ListItemText 
              primary={<Typography className={classes.commentName}>{comment.name||'no name'}</Typography>}
              secondary={<Typography className={classes.commentContent}>{comment.content}</Typography>}
            />
            <Link className={classes.timestamp} onClick={()=>{props.commentClickHandler(comment.sec)}}>{comment.timestamp}</Link>
          </ListItem>
        ))}
      </List>
    </div>
  );
}
export default CommentList;